import axios from 'axios';
// http Request Î∞è response URL
const config = {
    baseUrl: 'https://ghibliapi.herokuapp.com'
}

// Ï†ÑÏ≤ò API Îç∞Ïù¥ÌÑ∞ Ìò∏Ï∂ú Ìï®Ïàò
function fetchAllApi() {
    // axios Ìò∏Ï∂úÏùò Í≤∞Í≥ºÎäî Promise Í∞ùÏ≤¥Ïù¥ÎØÄÎ°ú 
    // then(), catch() Î•º ÌÜµÌï¥ Í≤∞Í≥º ÌôïÏù∏ Í∞ÄÎä•
    return axios.get(`${config.baseUrl}/films`)
}
// ÏòÅÌôîÏùò ÏÉÅÏÑ∏ Ï†ïÎ≥¥ Ìò∏Ï∂ú
function fetchDetailInfo(_id){
    return axios.get(`${config.baseUrl}/films/${_id}`)
}


export {
    fetchAllApi,fetchDetailInfo
}